import React, { Component } from 'react';
import Slider from 'react-slick';
import Page from 'react-slick';


export default class Blog extends Component {
    state={
        projects:[
            {
                date:"September 18, 2019",
                title:"Assignment 3 Updated",
                info:"Assignment 3 has been updated and resubmitted."
            },
            {
                date:"September 16, 2019",
                title:"Assignment 3 Posted",
                info:"Assignment 3 has been posted, which contains two forms: one implemented within the React framework and the other being a standalone HTML/JS form. The React form is missing one of the key features outlined in the instructions, so the HTML form has been added for grading purposes."
            },
            {
                date:"September 9, 2019",
                title:"Assignment 2 Posted",
                info:"Assignment 2 has been posted, which contains the ADA and W3C verification for this website."
            },
            {
                date:"September 8, 2019",
                title:"Metadata updated",
                info:"Metadata has been updated, including description tags and keywords."
            },
            {
                date:"August 28, 2019",
                title:"Upcoming Progress",
                info:"I will continue to work on improving the design and functionality of my website for Assignment 2. That includes finishing all of the Library pages (Blog, Documentation, Resources), finishing the About Me page, adding a Recent Entries section to the main page, and adding Assignment 2 to the Projects page. I will be using React and React Bootstrap for the website design while using JQuery and other libraries for more specific purposes such as assignments."
            },
            {
                date:"August 25, 2019",
                title:"Assignment 1 Complete",
                info:"Assignment 1 has been added, which outputs hello world in a JQuery example."
            },
            {
                date:"August 24, 2019",
                title:"Content Added",
                info:"Basic content is being added to the website."
            },
            {
                date:"August 23, 2019",
                title:"Main Pages Completed",
                info:"The index page, projects page, library page, and about me pages are completed and assignment 1 content has been added."
            },
            {
                date:"August 22, 2019",
                title:"Website Launched",
                info:"The website has been launched. All pages are under construction."
            },
            {
                date:"August 21, 2019",
                title:"Classes Begin",
                info:"Classes have officially started. Time to get to work."
            },
        ]
    }
    
    render() {
        return (
            <section className="blogsection">
            <div className="blogbanner"><h2>Development Blog</h2></div>
            <br></br>
            <br></br>
            <div className="box">
            <Slider
            speed={100}
            slidesToShow={1}
            slidesToScroll={1}
            infinite={false}
            >

            <div className="projects-center">
            <Page>
                    <div className="box">
                    <h3>{this.state.projects[0].title}</h3>
                    <h6>{this.state.projects[0].date}</h6>
                    <br></br>
                    <p>{this.state.projects[0].info}</p>
                    </div>
            </Page>
            <Page>
                    <div className="box">
                    <h3>{this.state.projects[1].title}</h3>
                    <h6>{this.state.projects[1].date}</h6>
                    <br></br>
                    <p>{this.state.projects[1].info}</p>
                    </div>
            </Page>
            <Page>
                    <div className="box">
                    <h3>{this.state.projects[2].title}</h3>
                    <h6>{this.state.projects[2].date}</h6>
                    <br></br>
                    <p>{this.state.projects[2].info}</p>
                    </div>
            </Page>
            </div>
            </Slider>
            </div>
            </section>
        );
    };
};