import React from 'react';
import Hero from "./components/Hero";
import Banner from "./components/Banner";
import Resources from "./Resources";
import {Link} from "react-router-dom";


const BlogPage = () => {
    return (
        <>
        <Hero hero="libraryHero">
            <Banner title="Library">
            <Link to = "/BlogPage" className="dropdown-content">
               Development Blog
            </Link>
            <Link to ="/library" className="dropdown-content">
                Documentation
            </Link>
            <a href="#content" className="dropdown-content">
                Resources
            </a>
            <a href="https://www.daytonastate.edu" className="dropdown-content">
                Daytona State College
            </a>
            </Banner>
        </Hero>
        <div id="content">
                <Resources />
            </div>
        </>
    )
}

export default BlogPage