import React from 'react';
import Hero from "./components/Hero";
import Banner from "./components/Banner";
import LibContent from "./LibContent";
import {Link} from "react-router-dom";


const Library = () => {
    return (
        <>
        <Hero hero="libraryHero">
            <Banner title="Library">
            <Link to = "/BlogPage" className="dropdown-content">
                Development Blog
            </Link>
            <a href="#content" className="dropdown-content">
                Documentation
            </a>
            <Link to ="/ResourcesPage" className="dropdown-content">
                Resources
            </Link>
            <a href="https://www.daytonastate.edu" className="dropdown-content">
                Daytona State College
            </a>
            </Banner>
        </Hero>
        <div id="content">
        <LibContent />
        </div>
        </>
    )
}

export default Library