                outcome:"This game is based on a popular card game called Skip-Bo. Each player has their own deck, their own stockpile, and a shared play area. The goal of the game is to place as many cards as possible onto the play area in ascending order, starting from 1 and ending at 10. When all three play areas end with Card 10, the game ends and the player with the least remaining cards wins. Player 1 goes first and draws up to five cards. Once five cards are drawn, the player can play those cards. When a player is done playing his or her turn, the player must end the turn by placing a card onto the stockpile. Once the player places a card onto the stockpile, that players turn is over and it is the second players turn to draw five cards. There is a wild card called the Parrot card, which can be placed onto any card in the play area and allows the player to place any card on top of the Parrot card. The Parrot card allows a player to decide whether to place a high card onto the Parrot and bring the game to a quicker end, or to place a low card onto the Parrot and reset the order, allowing for a longer game and giving the losing player an opportunity for a comeback.",
