import React from "react";
import ReactDOM from "react-dom";
import $ from 'jquery';
import "./App.css";

const txtFieldState = {
  value: "",
  valid: true,
  typeMismatch: false,
  errMsg: ""
};

const Field = ({
  valid,
  type,
  fieldId,
  fieldName,
  typeMismatch,
  formatErrorTxt,
  requiredTxt
}) => {

  return (
    <>
      <input type={type} name={fieldId} placeholder={fieldName} required />
      <br />
    </>
  );
};

class Form extends React.Component {
  componentDidMount() {
    $('#reactform').css('height','60px');
    $('#htmlform').css('height','60px');
    $('#reactform').css('width','350px');
    $('#htmlform').css('width','350px');
    $('#reactform').css('border','2px solid red');
    $('#htmlform').css('border','2px solid blue')
    $('#reactform').css('overflow', 'nowrap');
    $('#htmlform').css('overflow', 'nowrap');
    $('#output').css('min-width','60vw');
    $('#center').css('min-width','40vw');
    $('#output').css('display','none');
    $('#display').css('display','none');
    $('#display').css('height','45px');
    $('#form1').css('display','none');
    $('#form1').css('min-width','50vw');
    $('#form1').css('font-size','11pt');
    $('#reactform').click(function(){
      $('#display').css('display','block');
      $('#form1').css('display','block');
      $('#htmlform').css('display','none');
      $('#reactform').css('display','none');
    });
    $('#display').click(function(){
      if ( $('#form1').css('display') === 'block' ) {
        $('#output').css('display','block');
        $('#form1').css('display','none');
        $('#center').css('display','none');
      }
      else {
        $('#output').css('display','none');
        $('#form1').css('display','block');
      }
    });
   }
    
  
  state = {
    email: {
      ...txtFieldState,
      fieldName: "Email",
      required: true,
      requiredTxt: "Email is required",
      formatErrorTxt: "Incorrect email format",
      type: "email"
    },
    firstname: {
      ...txtFieldState,
      fieldName: "First Name",
      required: true,
      requiredTxt: "First Name is required",
      type: ""
    },
    lastname: {
      ...txtFieldState,
      fieldName: "Last Name",
      required: true,
      requiredTxt: "Last Name is required",
      type: "text"
    },
    street: {
      ...txtFieldState,
      fieldName: "Street Number",
      required: true,
      requiredTxt: "Address is required",
      type: "number"
    },
    streetname: {
      ...txtFieldState,
      fieldName: "Street Name",
      required: true,
      requiredTxt: "Address is required",
      type: "text"
    },
    city: {
      ...txtFieldState,
      fieldName: "City",
      required: true,
      requiredTxt: "Address is required",
      type: "text"
    },
    st: {
      ...txtFieldState,
      fieldName: "State Abbreviation",
      required: true,
      requiredTxt: "Address is required",
      type: "text"
    },
    zip: {
      ...txtFieldState,
      fieldName: "Zip Code",
      required: true,
      requiredTxt: "Address is required",
      type: "number"
    },
    phone: {
      ...txtFieldState,
      fieldName: "Phone",
      required: true,
      requiredTxt: "Phone is required",
      type: "number"
    },
    birthdate: {
      ...txtFieldState,
      fieldName: "8-digit Birthdate",
      required: true,
      requiredTxt: "Birthdate is required",
      type: "date"
    },
    message: {
      ...txtFieldState,
      fieldName: "Message",
      required: true,
      requiredTxt: "Message is required",
      type: "text"
    },
    confirm: {
      ...txtFieldState,
      fieldName: "Verification Code",
      required: true,
      requiredTxt: "Verification code is required",
      type: "text"
    },
    allFieldsValid: false,
    valcodes: ["apple", "orange", "silver", "gold", "Napoleon", "Bonaparte", "Tacitus", "Socrates", "Polybius", "Archimedes", "Caesar", "Augustus", "Claudius", "Gilgamesh", "Cleopatra"]
  };

  reduceFormValues = formElements => {
    const arrElements = Array.prototype.slice.call(formElements);
    const formValues = arrElements
      .filter(elem => elem.name.length > 0)
      .map(x => {
        const { typeMismatch } = x.validity;
        const { name, type, value } = x;

        return {
          name,
          type,
          typeMismatch,
          value,
          valid: x.checkValidity()
        };
      })
      .reduce((acc, currVal) => {

        const { value, valid, typeMismatch, type } = currVal;
        const { fieldName, requiredTxt, formatErrorTxt } = this.state[
          currVal.name
        ]; 

        acc[currVal.name] = {
          value,
          valid,
          typeMismatch,
          fieldName,
          requiredTxt,
          formatErrorTxt
        };

        return acc;
      }, {});

    return formValues;
  };

  checkAllFieldsValid = formValues => {
    return !Object.keys(formValues)
      .map(x => formValues[x])
      .some(field => !field.valid);
  };

  onSubmit = e => {
    e.preventDefault();
    const form = e.target;

    const formValues = this.reduceFormValues(form.elements);
    const allFieldsValid = this.checkAllFieldsValid(formValues);
    this.setState({ ...formValues, allFieldsValid });
  };

  mapFieldInputs = () => {
    return Object.keys(this.state)
      .filter(x => x !== "allFieldsValid")
      .map(field => {
        return {
          fieldId: field,
          ...this.state[field]
        };
      });
  };

  showvals = () => {
    let confstate = "";
    if (this.state.allFieldsValid && !this.state.valcodes.includes(this.state.confirm.value)) {
      confstate = "Incorrect validation code";
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    if (this.state.zip.value.length !== 5) {
      confstate += "Invalid zip. Zip must be 5 digits. \n";
      if (this.state.allFieldsValid) {
          this.setState({allFieldsValid: false});
        }
    }
    if (this.state.st.value.length !== 2) {
      confstate += "Invalid state abbreviation. \n";
      if (this.state.allFieldsValid) {
          this.setState({allFieldsValid: false});
        }
    }
    if (this.state.phone.value.length > 10 || this.state.phone.value.length < 7) {
      confstate += "Invalid. Phone number must be between 7 and 10 digits. \n";
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    if (this.state.birthdate.value.length > 10) {
      confstate += "Invalid. Birthdate must be 8 digits. \n";
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    let bdate = String(this.state.birthdate.value);
    let year = bdate[0] + bdate[1] + bdate[2] + bdate[3];
    if (year < 1900 || year > 2018) {
      confstate += "Invalid birthdate: invalid year. \n";
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    var lettercheck = /^[a-zA-Z]+$/;
    var letterspacecheck = /^[a-zA-Z ]+$/;
    if (!this.state.firstname.value.match(lettercheck) || !this.state.firstname.value.match(lettercheck)) {
      confstate += "Invalid. Name must contain only letters."
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    if (!this.state.streetname.value.match(letterspacecheck) || !this.state.city.value.match(letterspacecheck) || !this.state.st.value.match(lettercheck)) {
      confstate += "Invalid. Please provide a valid address."
      if (this.state.allFieldsValid) {
        this.setState({allFieldsValid: false});
      }
    }
    
    else {
        if (this.state.allFieldsValid && this.state.valcodes.includes(this.state.confirm.value)) {
        confstate = "Valid";
      }
  }
    return confstate;
  }
  render() {
    const { allFieldsValid } = this.state;
    const successFormDisplay = allFieldsValid ? "block" : "none";
    const inputFormDisplay = !allFieldsValid ? "block" : "none";
    const fields = this.mapFieldInputs();
    let fieldnames = [["Email"], ["First Name"], ["Last Name"], ["StreetNo"], ["Street Name"], ["City"], ["State"], ["Zip Code"], ["Phone"], ["Birthdate"], ["Message"], ["Verification: Enter the verification code provided below"]];
    let renderFields = fields.map(x => <Field {...x} />);

    for (var i in fieldnames) {
      fieldnames[i].push(renderFields[i]);
    }

    let val = "";

    val = this.showvals();

    let valcode = this.state.valcodes[Math.floor(Math.random()*this.state.valcodes.length)];

    return (
     
      <>
          <a href="./form.html"><div className="dropdown-content" id="htmlform" >HTML Form <br></br>(Submitted For Grading)</div></a>
          <div id="center" style={{ display: successFormDisplay }}>
          <br></br>
          <br></br>
          <br></br>
          <p>Name: {this.state.firstname.value} {this.state.lastname.value}</p>
          <p>Email: {this.state.email.value}</p>
          <p>Phone: {this.state.phone.value}</p>
          <p>Address: {this.state.street.value} {this.state.streetname.value}, {this.state.city.value}, {this.state.st.value} {this.state.zip.value}</p>
          <p>Birthdate: {this.state.birthdate.value}</p>
          <p>Validation Code: {this.state.confirm.value}</p>
          <p>Validity: {val}</p>
          <p style={{ textAlign: "center" }}>
            You have successfully submitted this form.
          </p>
          <br></br>
        </div>
          <div className="projects-center" id="output">
          <br></br>
          <br></br>
          
          </div>
          <div className="dropdown-content" id="reactform">React Form <br></br>(Not complete for submission)</div>
          
          <div id="form1">
        <div className="form-input" style={{ display: inputFormDisplay }}>
          <form action="mailto:mafila@gmail.com" method="post" enctype="text/plain"
            className="form-inside-input"
            onSubmit={this.onSubmit}
          >
          <br></br>
          {fieldnames} 
          <div className="val">{valcode}</div>
          <br></br>
          <br></br>
            <input id="submit" type="submit" value="Submit" action="mailto:mafila@gmail.com" onclick="location.href='mailto:mafila@gmail.com';" className="dropdown-content"/>
          </form>
          </div>
          
        </div>
      </>
    
    );
    };
}


export default Form;

const rootElement = document.getElementById("root");
ReactDOM.render(<Form />, rootElement);