import React, { Component } from 'react';
import {FaLaptopCode, FaGithub, FaBookOpen, FaUniversity} from "react-icons/fa";
import {Link} from "react-router-dom";

export default class Features extends Component {
    state={
        projects:[
            {
                icon:<Link to="/projects"><FaLaptopCode size="40px" /></Link>,
                title:"Code",
                info:"Academic and personal programming projects specializing in web development."
            },
            {
                icon:<Link to="/BlogPage"><FaBookOpen size="40px" /></Link>,
                title:"Read",
                info:"Documentation is provided for all projects, including academic notes, project comments, and blog entries."
            },
            {
                icon:<Link to="/ResourcesPage"><FaUniversity size="40px" /></Link>,
                title:"Learn",
                info:"Discover the resources that have worked for me and view project-related educational material."
            },
            {
                icon:<a href="https://github.com/YevgeniyBorts/"><FaGithub size="40px" /></a>,
                title:"Github",
                info:"View my Github repository and browse project folders."
            }
        ]
    }
    render() {
        return (
            <section className="projects">
            <div className="projects-center">
            {this.state.projects.map((item, index) => {
                return (
                    <article key={index} className="projects">
                    <span>{item.icon}</span>
                    <h6>{item.title}</h6>
                    <p>{item.info}</p>
                </article>
            );
            })}
            
            </div>
            </section>
        );
    };
};