import React, { Component } from 'react';
import {FaGlobe, FaLaptopCode, FaYoutube, FaBookOpen, FaUniversity, FaGithub} from "react-icons/fa";

export default class Resources extends Component {
    state={
        projects:[
            {
                icon:<a href="https://www.freecodecamp.org"><FaLaptopCode size="40px" /></a>,
                name:"freecodecamp.org",
                url:"https://www.freecodecamp.org",
                title:"Free Code Camp",
                info:"Very useful coding resources, tutorials, and an amazing Youtube channel."
            },
            {
                icon:<a href="https://www.hackerrank.com"><FaLaptopCode size="40px" /></a>,
                name:"hackerrank.com",
                url:"https://www.hackerrank.com",
                title:"Hacker Rank",
                info:"The programming challenges helped me get more familiarized with languages such as Python and Java."
            },
            {
                icon:<a href="https://www.humblebundle.com"><FaBookOpen size="40px" /></a>,
                name:"humblebundle.com",
                url:"https://www.humblebundle.com",
                title:"Humble Bundle",
                info:"Humble Bundle is a donation-based website that offers great deals on educational e-books for programming and cybersecurity."
            },
            {
                icon:<a href="https://www.w3schools.com"><FaGlobe size="40px" /></a>,
                name:"w3schools.com",
                url:"https://www.w3schools.com",
                title:"W3 Schools",
                info:"W3 Schools provided me with very useful references for CSS, HTML, JavaScript, and JQuery."
            },
            {
                icon:<a href="https://www.youtube.com/user/derekbanas/videos"><FaYoutube size="40px" /></a>,
                name:"youtube.com/derekbanas",
                url:"https://www.youtube.com/derekbanas",
                title:"Derek Banas",
                info:"This Youtube channel contains hundreds of tutorial videos covering nearly every major programming language."
            },
            {
                icon:<a href="https://www.youtube.com/channel/UCtxCXg-UvSnTKPOzLH4wJaQ/videos"><FaYoutube size="40px" /></a>,
                name:"youtube.com/codingtech",
                url:"https://www.youtube.com/codingtech",
                title:"Coding Tech",
                info:"This Youtube channel has very informative videos regarding many different aspects of programming."
            },
            {
                icon:<a href="https://www.youtube.com/user/TechGuyWeb/videos"><FaYoutube size="40px" /></a>,
                name:"youtube.com/traversymedia",
                url:"https://www.youtube.com/traversymedia",
                title:"Traversy Media",
                info:"This Youtube channel has a great variety of web development tutorials and crash course videos for beginners."
            },
            {
                icon:<a href="https://ocw.mit.edu/index.htm"><FaUniversity size="40px" /></a>,
                name:"ocw.mit.edu",
                url:"https://ocw.mit.edu/index.htm",
                title:"MIT Open Courseware",
                info:"Free classes from MIT including full lectures, lecture notes, assignments, and other course materials."
            },
            {
                icon:<a href="https://www.github.com"><FaGithub size="40px" /></a>,
                name:"github.com",
                url:"https://www.github.com",
                title:"Github",
                info:"The world's leading software development platform and largest open-source community."
            },
            {
                icon:<a href="https://www.codeacademy.com"><FaLaptopCode size="40px" /></a>,
                name:"codeacademy.com",
                url:"https://www.codeacademy.com",
                title:"Code Academy",
                info:"One of the most popular websites to learn code for free."
            }
        ]
    }
    
    render() {
        return (
            <section className="projects">
            <div className="projects-center">
            {this.state.projects.map((item, index) => {
                return (
                    <article key={index} className="projects">
                    <p>{item.icon}</p>
                    <a href = {item.url}>{item.name}</a>
                    <br></br>
                    <br></br>
                    <h6>{item.title}</h6>
                    <p>{item.info}</p>
                </article>
            );
            })}
            
            </div>
            </section>
        );
    };
};