import React, { Component } from 'react';
import {FaBlog, FaBook, FaTools, FaUniversity} from "react-icons/fa";

export default class LibContent extends Component {
    state={
        projects:[
            {
                icon:<FaBlog size="40px" />,
                title:"Development Blog",
                info:"A development blog is published alongside each update, detailing the progress of academic projecs, personal projects, and overall progress."
            },
            {
                icon:<FaBook size="40px" />,
                title:"Documentation",
                info:"Project documentation includes academic notes, project comments, blog entries, and links to external sources."
            },
            {
                icon:<FaTools size="40px" />,
                title:"Resources",
                info:"External resources are listed with links provided, including educational content and project tools."
            },
            {
                icon:<FaUniversity size="40px" />,
                title:"DSC",
                info:"Search for additional information, resources, and program-related material on the Daytona State College website."
            },
        ]
    }
    
    render() {
        return (
            <section className="projects">
            <div className="projects-center">
            {this.state.projects.map((item, index) => {
                return (
                    <article key={index} className="projects">
                    <span>{item.icon}</span>
                    <h6>{item.title}</h6>
                    <p>{item.info}</p>
                </article>
            );
            })}
            
            </div>
            </section>
        );
    };
};